import linkIcon from "assets/img/buttonIcons/link.svg";
import LoaderIcon from "assets/img/buttonIcons/loader.png";
import MenuBox from "components/FileUploadMenuBox";
import IconButton, { BUTTON_TYPES } from "components/IconButton";
import Text from "components/Text";
import React, { ReactNode, useState } from "react";
import { PAGE_MODE } from "utils/Constant";

interface IProps {
  children: React.ReactNode;
  className?: string;
  disabled?: boolean;
  disabledMenu?: boolean;
  disablePrePopulate?: boolean;
  headerWrapperClassName?: string;
  kebabModel?: any;
  onCancel?: () => void;
  onEdit?: () => void;
  onPrePopulate?: () => any;
  onLink?: () => any;
  onSave?: () => void;
  pageMode: string;
  renderHeaderContent?: () => any;
  title?: string | "";
  titleArgs?: any;
  variant?: string;
}

const SaveComponent = (props: IProps) => {
  const {
    children,
    className,
    disabled,
    disabledMenu,
    disablePrePopulate,
    headerWrapperClassName,
    kebabModel,
    onCancel,
    onEdit,
    onPrePopulate,
    onLink,
    onSave,
    pageMode,
    renderHeaderContent,
    title,
    titleArgs,
    variant,
  } = props;

  const [showPrePopulateLoader, setShowPrePopulateLoader] = useState(false);

  const onHandlePrePopulate = async () => {
    if (onPrePopulate) {
      const res = onPrePopulate();
      setShowPrePopulateLoader(true);
      if (res && Promise.resolve(res) === res) {
        res
          .then(() => {
            setShowPrePopulateLoader(false);
          })
          .catch(() => {
            setShowPrePopulateLoader(false);
          });
      }
    }
  };

  /* 
    ------------------------------------------------------------------------------------------------
    Sectional Render Functions: For Sub components / section wise renders
    ------------------------------------------------------------------------------------------------
  */

  //loader icon
  const renderLoaderIcon = (): ReactNode => {
    return (
      <span>
        <img className="w-3 h-3 animate-spin" src={LoaderIcon} alt="loader" />
      </span>
    );
  };
  //

  /* 
    -----------------------------------------------------------------------------------------
    render: The Main return()
    -----------------------------------------------------------------------------------------
  */
  return (
    <div
      className={`border bor ${
        pageMode !== PAGE_MODE.VIEW ? "border-primary-sapphireBlue-600" : "border-primary-ptext-500"
      }  rounded-xl p-5 flex flex-col gap-3 ${className ?? ""}`}
    >
      <div className={` flex items-center justify-between  ${headerWrapperClassName ?? ""}`}>
        {renderHeaderContent ? (
          renderHeaderContent?.()
        ) : (
          <Text
            label={title}
            args={titleArgs}
            className="text-primary-sideBarBgColor text-sm font-bold mb-1"
          />
        )}

        <div className="flex items-center justify-end gap-3">
          {onLink ? (
            <button
              type="button"
              title="link"
              className={`flex gap-1.5 items-center select-none`}
              onClick={onLink}
            >
              <img src={linkIcon} alt="link-icon" />
              <span className="flex items-center gap-0.5">
                <Text
                  className={`text-sm  ${"text-primary-sapphireBlue-600 cursor-pointer"}`}
                  label={"component.btn.link"}
                />
              </span>
            </button>
          ) : null}

          {pageMode === PAGE_MODE.VIEW && (
            <IconButton
              buttonType={BUTTON_TYPES.EDIT}
              onClick={onEdit}
              disabled={disabled}
              tooltip="Edit"
              tooltipOptions={{
                position: "top",
                className: "text-xs font-Roboto bg-black text-white !p-1 rounded-md ",
                showDelay: "1000,",
                hideDelay: "300",
              }}
            />
          )}

          {(pageMode === PAGE_MODE.CREATE || pageMode === PAGE_MODE.EDIT) && (
            <>
              {onPrePopulate ? (
                <button
                  type="button"
                  title="Pre Populate OCR"
                  className={`flex gap-1.5 items-center select-none`}
                  onClick={
                    showPrePopulateLoader || disablePrePopulate ? undefined : onHandlePrePopulate
                  }
                >
                  {showPrePopulateLoader && renderLoaderIcon()}
                  <span className="flex items-center gap-0.5">
                    <Text
                      className={`text-sm   ${
                        showPrePopulateLoader || disablePrePopulate
                          ? "pointer-events-none text-primary-gray-400 cursor-not-allowed"
                          : "text-primary-sapphireBlue-800 cursor-pointer"
                      }`}
                      label={"component.pre.populate.OCR"}
                    />
                  </span>
                </button>
              ) : null}

              <IconButton
                buttonType={BUTTON_TYPES.CANCEL}
                onClick={onCancel}
                tooltipOptions={{
                  position: "left",
                  className: "text-xs font-Roboto bg-black text-white !p-1 rounded-md ",
                  showDelay: "1000,",
                  hideDelay: "300",
                }}
              />
              <IconButton
                buttonType={BUTTON_TYPES.SAVE}
                onClickWithLoader={onSave}
                tooltipOptions={{
                  position: "top",
                  className: "text-xs font-Roboto bg-black text-white !p-1 rounded-md ",
                  showDelay: "1000,",
                  hideDelay: "300",
                }}
              />
            </>
          )}

          <MenuBox model={kebabModel} variant="SECONDARY" disabled={disabledMenu} />
        </div>
      </div>

      {children}
    </div>
  );
};

export default SaveComponent;
